.header {
  position: relative;
  background: url(../images/hero-image.jpg) no-repeat center / cover;
  min-height: 920px;
  font-family: 'Roboto', sans-serif;
}
.header:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 540px;
  height: 540px;
  background: url(../images/decor/triangle-top-left.png) no-repeat;
}
.header:after {
  content: '';
  position: absolute;
  right: 15px;
  top: 0;
  width: 950px;
  height: 375px;
  background: url(../images/decor/triangle-top.png) no-repeat;
}
.header__top {
  position: relative;
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}
.header__logo {
  display: flex;
  align-items: center;
}
.header__logo img {
  margin-right: 20px;
}
.header__link {
  margin-right: 25px;
}
.header__center {
  position: relative;
  padding-top: 240px;
  text-align: center;
  z-index: 10;
}
.header__title {
  font-size: 60px;
  font-weight: 700;
}
.header__desc {
  padding-top: 75px;
  font-size: 38px;
  color: #c8d1dc;
}
.header__end {
  padding-top: 180px;
}
.header .scrollLink {
  display: block;
  margin: 0 auto;
  animation: growLine 3s infinite;
}
.header .scrollLink:hover {
  animation-play-state: paused;
}
.advantages {
  background: url(../images/advantages__back.jpg) no-repeat center / cover;
}
.advantages__title:before,
.advantages__title:after {
  position: absolute;
  content: '';
  top: 50%;
  transform: translateY(-50%);
  height: 1px;
  width: 77px;
  background: #afafaf;
}
.advantages__title:before {
  left: 250px;
}
.advantages__title:after {
  right: 250px;
}
.advantages__list {
  padding-top: 70px;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}
.advantages__item {
  text-align: center;
  color: #000;
}
.advantages .imgWrap {
  min-height: 240px;
}
.advantages .shdw {
  position: relative;
  z-index: 10;
}
.advantages .shdw:before {
  content: '';
  display: block;
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  width: 180px;
  height: 180px;
