.slick-prev,
.slick-next {
	width: 50px;
	height: 100%;
	opacity: 1;
	transition: opacity .3s linear;
	z-index: 10;
}


.slick-prev {
	bottom: -55px;
	left: 450px; 
	.bg-arrow_left
}

.slick-next {
	bottom: -55px;
	right: 30px; 
	.bg-arrow_right
}


.slick-prev:before,
.slick-next:before {
	width: 0;
	height: 0;
}

.slick-dots {
	display: inline-block!important;
	left: -48px;
	top: 50%;
	transform: translateY(17%);

	border-image: transparent;

	li {
		margin: 4px 8px;
		margin-bottom: 10px;

		width: 13px;
		height: 13px;

		button {
			background: #fff;
			display: block;
			height: 13px;
			width: 13px;
			font-size: 0px;
			color: #fff;
			border-radius: 50%;
			box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
			opacity: 0.75;
			&:before {
				font-size: 0;
				opacity: 0;
				display: none;
			}

		}
		&.slick-active button {
			background: #fb0035;
			border-radius: 50%;
			opacity: 1;
			@media @tablet {
				background: #fff;
			}
		}
	}
}


.slick-current, .slick-slide, .slick-active { 
	outline: none!important;
}